.PopUpWindow-outer-wrapper{
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    background-color: #222222;
    opacity: 0.5;
}

.PopUpWindow-inner-wrapper{
    padding: 15px;
    border-radius: 5px;
    opacity: 1;
    width:35%;
    height: 230px;
    background: #ffffff;
    background: -webkit-gradient(linear, left top, right bottom, from(#232323), to(#a09fa6));
    background: -webkit-linear-gradient(top left, #232323, #a09fa6);
    background: -o-linear-gradient(top left, #232323, #a09fa6);
    background: linear-gradient(to bottom right, #232323, #a09fa6);
    position: fixed;
    top:0;
    bottom: 0;
    left: 0;
    right: 0;

    margin: auto;
}

@media screen and (max-width: 740px) {
    .PopUpWindow-outer-wrapper{
        width: 100vw;
        height: 100vh;
    }
    .PopUpWindow-inner-wrapper{
        width:90%;
    }
}


