.BookHeaders-wrapper{
    padding: 0.5em;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    background-color: white;
    border-radius: 0.3em;
    margin: 0.3em;
}

.BookHeaders-wrapper > span {
    font-family: 'Acme', sans-serif;
    -ms-flex: 1.5 1;
        flex: 1.5 1;
}

.BookHeaders-wrapper > span:first-child {
    -ms-flex: 1 1;
        flex: 1 1;
}
.BookHeaders-wrapper > span:nth-child(4) {
    -ms-flex: 4 1;
        flex: 4 1;
}


.BookHeaders-icon {
    cursor: pointer;
}

.BookHeaders-icon-replacement{
    width: 2.1em;
}

@media screen and (max-width: 740px) {
    .BookHeaders-wrapper{
        display: none;
    }
}


