.Book-wrapper{
    padding: 0.5em;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    background-color: white;
    border-radius: 0.3em;
    margin: 0.3em;
}

.Book-wrapper > p {
    -ms-flex: 1.5 1;
        flex: 1.5 1;
}

.Book-wrapper > p:first-child {
    -ms-flex: 1 1;
        flex: 1 1;
}
.Book-wrapper > p:nth-child(4) {
    -ms-flex: 4 1;
        flex: 4 1;
}

.Book-icon{
    cursor: pointer;
    margin: 0.1em;
}

@media screen and (max-width: 740px) {
    .Book-wrapper{
        -ms-flex-direction: column;
            flex-direction: column;
        background-image: url('../../assets/feather.jpg');
        background-repeat: no-repeat;
        background-size: 15%;
        background-position: right top;
    }
    .Book-wrapper > p {
        display: block;
        -ms-flex: 1 1;
            flex: 1 1;
        padding: 0.2em;
    }

    .Book-mobile-title{
        display: inline;
        font-weight: bold;
    }

    .Book-desktop-icon{
        display: none
    }
    .Book-icon-mobile-wrapper{
        display: block;
        margin-top: 2%;
    }
    .Book-wrapper .Book-icon {
        width: 48%;
        margin: 1%;
        background-color: #e1e1ea;
        padding:0.2em;
        font-size: 1.5em;
        border-radius: 0.3em;
    }
}



